@import '../fonts';

header {
    padding: 30px;
}

body {
    font-family: BigNoodle;
    background-color: #fafbff;
    color: #000046;
}

.header-wrapper {
    display: grid;
    grid-template-columns: 200px 1fr;
    align-items: center;
}

.nav-links {
    width: 800px;
    display: flex;
    margin: 0 auto;
}

.nav-links li a {
    font-family: BigNoodle;
}

.nav-links li:not(:last-child) {
    margin-right: 40px;
}

* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}


a {
    text-decoration: none;
}

li {
    list-style: none;
}

.container {
    max-width: 1440px;
    margin: auto;
}

.quote-information {
    padding: 30px;
    max-width: 50%;
    background-color: gray;
    border-radius: 20px;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    color: white;
    font-family: Louis-George-Cafe;
}

.quote-information-wrapper {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.information-title {
    display: block;
    font-size: 50px;
    margin-bottom: 30px;
}

.price {
    font-size: 28px;
}

.quote-information table {
    border-collapse: collapse;
    margin-bottom: 40px;
}

.quote-information table td {
    padding: 10px;
    border: 1px solid black;
    font-size: 20px;
}

footer {
    padding: 20px;
    text-align: center;
    background-color: black;
    color: white;
}

